<template>
  <div id="wrapper">
    <div class="big-box" v-show="isShow" >
      <div class="box">

          <router-link :to="{path:'/la'}" tag="p" class="back">×</router-link>
          <h2>支付记录</h2>
           <el-table ref="multipleTable" :data="dataAlert" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column label="支付单信息" width="120">
            <template scope="scope">
              <p>{{ scope.row.messageo }}</p>
              <p>{{ scope.row.messaget }}</p>
            </template>
          </el-table-column>
           <el-table-column label="收货人信息" width="145">
            <template scope="scope">
            <p>{{ scope.row.consigneename }}</p>
            <p class="blue">{{ scope.row.consigneenum }}</p>
          </template>
          </el-table-column>
          <el-table-column label="银行信息" width="130">
            <template scope="scope">
            <p>{{ scope.row.bankname }}</p>
            <p>{{ scope.row.bankadress }}</p>
          </template>
          </el-table-column>
          <el-table-column label="支付司机运费" width="130">
            <template scope="scope">
            <p class="red">{{ scope.row.price }}</p>
          </template>
          </el-table-column>
          <el-table-column label="申请时间" width="137">
            <template scope="scope">
            <p class="blue">{{ scope.row.dateo }}</p>
            <p class="blue">{{ scope.row.datet }}</p>
          </template>
          </el-table-column>
          <el-table-column label="付款时间" width="135">
            <template scope="scope">
            <p class="blue">{{ scope.row.dateth }}</p>
            <p class="blue">{{ scope.row.datef }}</p>
          </template>
          </el-table-column>
          </el-table>
          <router-link :to="{path:'/la'}" tag="button" class="backo">确定</router-link>
      </div>
    </div>

  </div>


</template>
<script>
export default {
data(){
  return{
    isShow:true,
    dataAlert:[{
      messageo:"455662655",
      messaget:"方式：保理",
      consigneename:"张光芒",
      consigneenum:'156955656565',
      bankname:"中国农业银行",
      bankadress:'天津',
      price:"12262.32",
      dateo:"2017-08-11",
      datet:"2017-09-02",
      dateth:"2017-09-02",
      datef:"2025-10-10"
    }]
  }
},
methods: {
}
}
</script>
<style lang="less" scoped>
#wrapper .box{
  width: 800px;
  height: 236px;
  background:#fff;
  border: 1px solid #999;
  position: relative;
  padding:20px 30px;
  h2{
    color:#333;
    font-size:20px;
  }
  .el-table{
    margin-top:40px;
    .blue{
      color:#9396A7;
    }
    .red{
      color:red;
    }
  }
}
#wrapper .big-box{
  padding:430px 480px;
  width:1672px;
  background:rgba(0,0,0,0.7);
  position:relative;
  z-index:2;
  top: 0;
  left:0;
}
#wrapper .box .back{
  text-align: center;
  line-height: 20px;
  position: absolute;
  right:40px;
  top: 20px;
  background: #fff;
  font-size:25px;
  color:#333;
}
#wrapper button {
    width: 90px;
    height: 40px;
    color: #fff;
    background: #0c9b0b;
    border-radius: 5px;
    margin-top:40px;
    position:absolute;
    right:40px;
}
</style>



